export default {
    name: 'RecentContacts',
    data() {
        return {
            mainHeading: 'Who are you sending the invoice to?',
            subHeading: 'Recents',
            skeletonLoading: true,
            skeletonCount: 5,
            skeletonAnimated: true,
            skeletonVariant: 'circle',
            formValidationSuccess: false,
        };
    },
    props: {
        hasBottomBorder: {
            type: Boolean
        },
    },
    computed: {
        recentContacts() {
            return this.$store.state.recentContacts
        }
    },
    methods: {
        getNameInitial(name) {
            return name.charAt(0);
        }
    },
    mounted() {
        setTimeout(() =>  // fake delay to show skeleton state
        {
            this.$store.dispatch("getRecentContacts")
            this.skeletonLoading = false
        }, 600)
    }
}