import Vue from 'vue'
import ElementUI from 'element-ui'
import Vuex from "vuex"
import axios from "axios"
import App from './App.vue'

Vue.use(Vuex)
Vue.use(ElementUI)

Vue.config.productionTip = false

const store = new Vuex.Store({
  state: {
    contacts: [],
    recentContacts: [],
  },
  mutations: {
    setContacts(state, data) {
      state.contacts = data;
    },
    setRecentContacts(state, data) {
      state.recentContacts = data;
    }
  },
  actions: {
    async getContacts(context) {
      const { data } = await axios.get(
        `/static/contacts.json`
      );
      context.commit("setContacts", data);
    },
    async getRecentContacts(context) {
      const { data } = await axios.get(
        `/static/recentContacts.json`
      );
      context.commit("setRecentContacts", data);
    }
  }
});

new Vue({
  store,
  render: h => h(App),
}).$mount('#app')
