import RecentContacts from '../RecentContacts/RecentContacts.vue'

export default {
    name: 'InvoiceDrawer',
    components: {
      RecentContacts,
    },
    data() {
      let checkDigits = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('Please enter 4 digit'));
        }
        setTimeout(() => {
          if (!Number.isInteger(value)) {
            callback(new Error('Value must be a number'));
          } else {
            if (value.length !== 4) {
              callback(new Error('4 digits required'));
            } else {
              callback();
            }
          }
        }, 1000);
      };
      return {
        showContactsPanel: false,
        selectContactText: 'Select from contacts',
        addEmailText: 'Add another email',
        cancelText: 'Cancel',
        nextText: 'Next',
        isFormValidationPending: true,
        form: {
          name: '',
          email: '',
          fourdigit: '',
        },
        rules: {
          fourdigit: [
            { validator: checkDigits, trigger: 'blur' }
          ],
        },
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      updateFormValidation() {
        let fields = this.$refs.contactForm.fields;
        if (fields.find((f) => f.validateState === 'validating')) {
          setTimeout(() => {
            this.updateIsFormValidated();
          }, 100);
        } else {
          this.isFormValidationPending = fields.reduce((acc, f) => {
            return acc && f.validateState === 'success';
          }, true);
        }
      },
      emitOpenContactsEvent() {
        this.$emit('openContactsPanel');
      }
    }
  }