import InvoiceDrawer from '@/components/InvoiceDrawer/InvoiceDrawer.vue'
import ContactPanel from '@/components/ContactPanel/ContactPanel.vue'

export default {
  name: 'Invoice',
  components: {
    InvoiceDrawer,
    ContactPanel
  },
  data() {
    return {
      buttonText: 'Invoice',
      drawerTitle: 'Create an invoice',
      contactDrawerTitle: 'Select contact',
      isDrawerVisible: false,
      isContactsDrawerVisible: false,
    };
  },
  methods: {
    openDrawer() {
      this.isDrawerVisible = true
    },
    handleOpenContactsPanel() {
      this.isContactsDrawerVisible = true;
    },
    hideContactPanel() {
      this.isContactsDrawerVisible = false;
    }
  }
}